import utils from '@/utils/utils.js';
export default {
	/*
	初始化数据
	*/
	data() {
		return {
			address: "",
			myLocation: null,
			sexList: ["先生", "女士"],
			sex: "",
			tagList: ["家", "公司", "学校"],
			tag: ""
		};
	},
	computed: {
		// 计算属性的 getter
	},
	onLoad: function() {
		// console.log("utils=====+++", utils)
	},
	onShow: function() {
		// console.log("pages/user/addAddress");
	},
	methods: {
		_chooseLocation() {
			var _this = this;
			uni.chooseLocation({
				success: function(res) {
					_this.address = res.name;
					_this.myLocation = res;
				}
			});
		},
		_chooseSex(value) {
			this.sex = value;
		},
		_chooseTag(value) {
			this.tag = value;
		},

		formSubmit: function(e) {
			var data = e.detail.value;
			
			//参数校验
			let tel = data.tel
			let username = data.username
			let phone = utils.checkPhone(tel)
			let doorplate = data.doorplate
			let address = data.address
			if (tel == "") {
				uni.showToast({
					title: "请填写手机号码",
					icon: 'none',
				})
				return false
			}
			if (!phone) {
				uni.showToast({
					title: "请填写正确的手机号码",
					icon: 'none',
				})
				return false
			}
			if (username == "") {
				uni.showToast({
					title: "请填写您的名字",
					icon: 'none',
				})
				return false
			}

			if (address == "") {
				uni.showToast({
					title: "请选择地址",
					icon: 'none',
				})
				return false
			}

			if (doorplate == "") {
				uni.showToast({
					title: "请填写详细地址",
					icon: 'none',
				})
				return false
			}


			data.sex = this.sex;
			data.tag = this.tag;

			console.log("表单提交数据：", data);
		}
	}
};
